import React from 'react';
import arch from "../ready/anchovy.png"
import bacon from "../ready/bacon.png"
import basil from "../ready/basil.png"
import chili from "../ready/chili.png"
import mozzarella from "../ready/mozzarella.png"
import mushroom from "../ready/mushroom.png"
import olive from "../ready/olive.png"
import onion from "../ready/onion.png"
import pepper from "../ready/pepper.png"
import pepperoni from "../ready/pepperoni.png"
import tomato from "../ready/tomato.png"
import store from '../reducer/display'


export default class Ingridients extends React.Component{
    render(){
        return(
            <div className='boardBack'>
                <div className="board">
                    <div className="base">
                        <img src={arch} alt="arch" className={store.getState().arch ? "display ingredients" : "notDisplay ingredients" } />
                        <img src={bacon} alt="bacon" className={store.getState().bacon ? "display ingredients" : "notDisplay ingredients"}  />
                        <img src={pepperoni} alt="pepperoni" className={store.getState().pepperoni ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={basil} alt="basil"  className={store.getState().basil ? "display ingredients" : "notDisplay ingredients"}/>
                        <img src={chili} alt="chili" className={store.getState().chili ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={mushroom} alt="mushroom" className={store.getState().mushroom ? "display ingredients" : "notDisplay ingredients"}  />
                        <img src={olive} alt="olive" className={store.getState().olive ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={onion} alt="onion" className={store.getState().onion ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={pepper} alt="pepper" className={store.getState().pepper ? "display ingredients" : "notDisplay ingredients"}  />
                        <img src={tomato} alt="tomato" className={store.getState().tomato ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={mozzarella} alt="mozzarella" className={store.getState().mozzarella ? "display ingredients" : "notDisplay ingredients"}/>
                    </div>
                </div>
            </div>
        )
    }
}


