import React from 'react';
import store from '../reducer/display'
import {addProduct} from '../reducer/actions'
export default class Tabs extends React.Component{
    render(){
        return(
            <div className='tabs'>
                <section>
                    <button value='arch' onClick={addProduct} className={store.getState().arch ? "active" : "notActive"}>Anchovies</button>
                    <button value='bacon' onClick={addProduct} className={store.getState().bacon ? "active" : "notActive"}>Bacon</button>
                    <button value='basil' onClick={addProduct} className={store.getState().basil ? "active" : "notActive"}>Basil</button>
                    <button value='chili' onClick={addProduct} className={store.getState().chili ? "active" : "notActive"}>Chili</button>
                </section>
                <section>
                    <button value='mozzarella' onClick={addProduct} className={store.getState().mozzarella ? "active" : "notActive"}>Mozzarella</button>
                    <button value='mushroom' onClick={addProduct} className={store.getState().mushroom ? "active" : "notActive"}>Mushroom</button>
                    <button value='olive' onClick={addProduct} className={store.getState().olive ? "active" : "notActive"}>Olive</button>
                    <button value='onion' onClick={addProduct} className={store.getState().onion ? "active" : "notActive"}>Onion</button>
                </section>
                <section>
                    <button value='pepper' onClick={addProduct} className={store.getState().pepper ? "active" : "notActive"}>Pepper</button>
                    <button value='pepperoni' onClick={addProduct} className={store.getState().pepperoni ? "active" : "notActive"}>Pepperoni</button>
                    <button value='tomato' onClick={addProduct} className={store.getState().tomato ? "active" : "notActive"}>Tomato</button>
                    <button value='disAll' onClick={addProduct}>DisAll</button>
                </section>


            </div>
        )
    }
}


