import React from 'react';
import ReactDOM from 'react-dom';
import store from './reducer/display'
import Ingridients from './components/ingridients'
import Recepies from './components/recepies'
import Tabs from './components/tabs'
import count1 from "./reducer/sum";
function validate(email) {
    let reg =  /^\w+@\w+\.\w{2,4}$/i;
    let address = email.target.value;
    if(reg.test(address) === false) {

        return false;
    }
    else return true;
}
class App extends React.Component{
    constructor(props){
        super(props)
        this.state={
            ok : false
        };
        this.validate = this.validate.bind(this);
    }
    validate(email) {
        let reg =  /^\w+@\w+\.\w{2,4}$/i;
        let address = email.target.value;
        if(reg.test(address) === false) {

            this.setState({
                ok: false
            });
        }
        else this.setState({
            ok: true
        });;
    }
    render(){
        console.log(store.getState())
        return(
            <div className="mainPart">
            <Ingridients />
                <div className='infoPart' >
                    <h1>Welcome to Pizza Creator!</h1>
                    <div className='registration'>
                        <div>
                    <label>Name</label>
                    <input type='text'/>
                        </div>
                        <br/>
                        <div>
                    <label>Email</label>
                    <input type='text' onChange={this.validate} className={this.state.ok ? 'ok' : 'again'}/>
                        </div>
                    <br/>
                        <div>
                    <label>Adress</label>
                     <input type='text'/>
                    </div>
                    </div>
                    <div onClick={count1}>
                    <Recepies />
                    <Tabs />
                    </div>
                    <div className='totalCount'>
                        <span>Total count: </span>
                    <p>{store.getState().sum}</p>
                    </div>
                </div>
            </div>
        )
    }
}

const render = () => {
    ReactDOM.render(<App/>, document.getElementById('root'));
};
store.subscribe(render);
render();



